@charset "utf-8";

.kolonsport-site {
  .store-cont {
    .store-search {
      input {
        &:-moz-placeholder {
          color: #000;
        }
        &:-ms-input-placeholder {
          color: #000;
        }
        &::-webkit-input-placeholder {
          color: #000;
        }
      }
      button {
        color: #000;
        border: .266vw solid #cfcfcf;
      }
    }
    .store-search-result {
      .title {
        padding-top: 3.733vw;
      }
      .store-list {
        .address {
          margin-top: 4vw;
          font-size: 3.733vw;
          letter-spacing: -1px;
        }
        .tel {
          font-size: 3.733vw;
          color: #898989;
          letter-spacing: -1px;
        }
        a {
          padding: 1.733vw 4vw;
          color: #000;
          border: 0.266vw solid #bfbfbf;
        }
      }
    }
  }
}
@media (min-width: 750px) {
  .kolonsport-site {
    .store-cont {
      .store-search {
        select {
          line-height: 81px;
        }
        button {
          border: 2px solid #cfcfcf;
        }
      }
      .store-search-result {
        .title {
          padding-top: 28px;
        }
        .store-list {
          .address {
            margin-top: 30px;
            font-size: 28px;
          }
          .tel {
            font-size: 28px;
          }
          a {
            padding: 13px 30px;
            border: 2px solid #bfbfbf;
          }
        }
      }
    }
  }
}